
import { mapGetters, mapActions } from "vuex";
import Toast from 'vux/src/components/toast/index.vue'

import formItem from "@/component/form-item.vue";
import userAPI from "@/api/user";

export default {
components: {
formItem,
Toast
},
name: "forgetPwd",
data() {
return {
token: "",
codetext: "获取验证码",
initClock: 9,
clock: 0,
disabled: false,
formList: {
phone: "",
code: "",
pwd: "",
rePwd: ""
},
checkList: {
phone: false,
code: false,
pwd: false,
rePwd: false
},
errorText: "",
showToast: false
};
},
methods: {
...mapActions(["setUser"]),
getCode() {
this.clock = this.initClock;
let that = this;
let phone = this.formList.phone;
if (!this.checkPhoneNumber(phone)) {
this.showMsg("请输入正确的手机号码！");
this.checkList.phone = true;
return;
}

if (that.codetext === "获取验证码") {
this.disabled = true;
this.codetext = "请" + that.clock + "秒后重试";
var setClock = setInterval(function() {
that.clock--;
that.codetext = "请" + that.clock + "秒后重试";
}, 1000);
setTimeout(() => {
clearInterval(setClock);
that.codetext = "获取验证码";
that.disabled = false;
}, this.clock * 1000);

userAPI.getPhoneVerificationCode({ phone: phone }).then(res => {
let data = res.data;
if (data.code === 1) {
this.token = data.result;
}

this.showMsg(data.description);
});
}
},
checkPhoneNumber(number) {
let myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
if (myreg.test(number)) {
return true;
}
return false;
},
checkIsBlank(str) {
if (str.replace(/(^s*)|(s*$)/g, "").length <= 0) {
return false;
}
return true;
},
checkPwd(password) {
let pwdreg = /^[0-9a-zA-Z]{6,20}$/;
if (pwdreg.test(password)) {
return true;
}
return false;
},
submitForm() {
if (!this.validateForm()) {
return;
}
let code = this.formList.code;
let pwd = this.formList.pwd;

let params = {
newPwd: pwd,
checkCode: code,
tokenId: this.token
};
userAPI.resetPasswordByPhone(params).then(res => {
var data = res.data;
console.log(data);
if (data.code === 1) {
this.showMsg("密码修改成功，请重新登录！");
setTimeout(() => {
userAPI.logout();
this.setUser(null);
this.$router.push("/");
}, 2000);
} else {
this.showMsg(data.description);
}
});
},

showMsg(msg) {
this.showToast = true;
this.errorText = msg;
},

validateForm() {
if (!this.checkPhoneNumber(this.formList.phone)) {
this.showMsg("请输入正确的手机号码！");
this.checkList.phone = true;
return false;
}

if (!this.checkIsBlank(this.formList.code)) {
this.showMsg("请输入验证码！");
this.checkList.code = true;
return false;
}

if (!this.checkPwd(this.formList.pwd)) {
this.showMsg("您输入的密码不符合规范！");
this.checkList.pwd = true;
return false;
}

if (this.formList.pwd !== this.formList.rePwd) {
this.showMsg("您两次输入的密码不一致！");

this.checkList.pwd = true;
this.checkList.rePwd = true;
return false;
}

return true;
},

setNormal(check) {
this.checkList[check] = false;
}
},
watch: {},
computed: {
...mapGetters([])
},
created() {},
mounted() {
let elem = document.querySelector("#vux_view_box_body");
setTimeout(() => {
elem.style.paddingBottom = "44px";
}, 10);
}
};
